.c-btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
border-width: 1px;
border-style: solid;
  border-color: #59B8FC;
  border-radius: ;
  border-color: #59B8FC;

  color: #59B8FC;
  font-size: 14px;
-webkit-transition: all 0.3s 0s;
transition: all 0.3s 0s;
-webkit-transition-timing-function: ease;
        transition-timing-function: ease;
}
.c-btn span {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.c-btn span + span{
  margin-left: 0.05rem;
  margin-bottom: -1px;
}
.c-btn-hover{
  background: #efefef
}
.c-btn-hover.c-btn-on {
  background-color: #2c99e6;
}
.c-btn-on{
  color: #fff;
  background-color: #59B8FC;
}
.c-btn-base {
  width: 0.86rem;
  height: 0.28rem;

  border-radius: 0.86rem;
}
.c-btn-sm {
  width: 0.75rem;
  height: 0.28rem;

  border-radius: 0.75rem;
}
.c-btn-lg {
  width: 1.29rem;
  height: 0.42rem;

  border-radius: 1.29rem;
}
#app .c-btn-disabled {
  color: #efefef;
  /*background-color: $btn-disabled;*/
  border-color: #efefef;
}
.c-btn-icon{
  font-size: calc(14px + 4px)
}
.c-btn-icon:before {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}



