.c-dialog {
  width: 2.62rem;
  max-height: 2rem;
  position: fixed;
  z-index: 101;
  background-color: #fff;
  top: 50%;
  left: 50%;
  padding: 0.3rem 0 0.4rem;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  box-sizing: border-box;
  border-radius: 0.02rem;
}
.c-dialog .c-dialog-title {
  text-align: center;
  color: #ff6375;
  margin: 0 0 0.3rem;
}
.c-dialog > .c-dialog-body {
  padding-bottom: 0.3rem;
}
.c-dialog > .c-dialog-body >div {
  max-height: 0.48rem;
  overflow: auto;
}
.c-dialog > .c-dialog-body >div > p {
  color: #999;
  text-align: center;
  margin: 0;
}
.c-dialog .c-dialog-footer > .c-dialog-buttons {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0.40rem;
  line-height: 0.40rem;
  border-top: 0.01rem #ccc solid;
}
.c-dialog .c-dialog-footer > .c-dialog-buttons > a {
  display: block;
  height: 100%;
  float: left;
  text-align: center;
  text-decoration: none;
  color: #666;
}
.c-dialog .c-dialog-footer > .c-dialog-buttons a+a {
  border-left: 0.01rem #ccc solid;
}
/**
 *一个按钮样式
 */
.c-dialog-button-1 > a {
  width: 100%;
}
/**
 *两个按钮样式
 */
.c-dialog-button-2 > a {
  width: 49.8%;
}
 /**
 *没有text样式
 */
.c-dialog > .c-dialog-body-empty{
  padding: 0 ;
}



