























































































.c-timepicker{
  position: relative;
}
.c-timepicker .c-timepicker-unit{
  position: absolute;
  top: 50%;
  width: 100%;
  height: 16px;
  -webkit-transform: translate(0, -55%);
          transform: translate(0, -55%);
  left: 0;
  font-size: calc(16px + 2px);
  pointer-events: none;
}
.c-timepicker .c-timepicker-unit .c-timepicker-unit-colon{
  position: absolute;
  left: 0;
  right: -0.21rem;
  margin: auto;
  margin-top: -1px;
  width: calc(16px + 2px);
  text-align: center;
}
.c-timepicker .c-timepicker-unit .c-timepicker-unit-hour{
  position: absolute;
  left: -0.68rem;
  right: 0;
  margin: auto;
  width: calc(16px + 2px);
  text-align: center;
}
.c-timepicker .c-timepicker-unit .c-timepicker-unit-min{
  position: absolute;
  left: 0;
  right: -1.60rem;
  margin: auto;
  width: calc(16px + 2px);
  text-align: center;
}





