
.c-actions{
  position: fixed;
  top: 100%;
  left: 0;
  right: 0;
  margin: auto;

  text-align: center;
  font-size: 18px;

  width: 3.1rem;

  z-index: 101;

  -webkit-transition: -webkit-transform .3s ease-out;

  transition: -webkit-transform .3s ease-out;

  transition: transform .3s ease-out;

  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}
.c-actions .c-actions-warpper{
  background: #fff;
  border-radius: 4px;
  margin-bottom: 12px;
}
.c-actions .c-actions-warpper > div{
  padding: 12px;
}
.c-actions .c-actions-item + .c-actions-item{
  border-top: 1px solid #999;
}
.c-actions.c-actions-active{
  -webkit-transform: translate(0, -100%);
          transform: translate(0, -100%);
}



