.c-timer a{
  text-decoration: none;
  color: #2c3e50;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-tap-highlight-color:rgba(0, 0, 0, 0);
}
.c-timer .u-relative{
  position: relative;
}
.c-timer .u-relative .c-timer-content{
  padding-right: 12px;
}
.c-timer .u-relative .icon{
  width: 16px;
  height: 16px;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0.06rem;
  margin: auto;
  -webkit-transform: translate(0, -4%);
          transform: translate(0, -4%);
}
.c-timer .c-timer-content{
  color: #999;
}
.c-timer .c-timer-center{
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.c-timer .c-timer-border-top{
  border-top: 1px solid #e5e5e5;
}
.c-timer .c-dayspicker-quick{
  padding: 0.12rem;
}
.c-timer .c-dayspicker-body{
  padding: 0 0.12rem;
}
.c-timer .c-timer-input{
  width: 100%;
}
