.fade-enter-active, .fade-leave-active  {
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.c-mode {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.c-mode-item {
  box-sizing: border-box;
  font-size: 12px;

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
-webkit-transition: background-color 0.3s 0s;
transition: background-color 0.3s 0s;
-webkit-transition-timing-function: ease;
        transition-timing-function: ease;

  border-left: 1px solid #eee;
  border-bottom: 1px solid #eee;
}
.c-mode-item-icon {
  font-size: 18px;
}
.c-mode-4 .c-mode-item {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: calc(100% / 4);
  height: calc(0.26rem * 3);
}
.c-mode-4 .c-mode-item-icon {
  margin-bottom: 0.1rem;
}
.c-mode-4 .c-mode-item:nth-child(4n + 1) {
  border-left-width: 0;
}
.c-mode-2 .c-mode-item  {
  width: calc(100% / 2);
  height: calc(0.26rem * 2);
}
.c-mode-2 .c-mode-item:nth-child(2n+1)  {
  border-left-width: 0;
}
.c-mode-2 .c-mode-item-icon  {
  margin-right: 0.05rem;
}
.c-mode-3 .c-mode-item  {
  width: calc(100% / 3);
  height: calc(0.26rem * 2);
}
.c-mode-3 .c-mode-item:nth-child(3n+1)  {
  border-left-width: 0;
}
.c-mode-3 .c-mode-item-icon  {
  margin-right: 0.05rem;
}
.c-mode-item-active {
  background: #59B8FC;
  color: #fff;
}

/* Toggle */
.c-mode-more-hook {
  height: calc(0.26rem * 2);
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.c-mode-toggle {
  margin-left: 0.05rem;
  font-size: 18px;
}
.icon-pull-down {
-webkit-transition: -webkit-transform 0.3s 0s;
transition: -webkit-transform 0.3s 0s;
transition: transform 0.3s 0s;
transition: transform 0.3s 0s, -webkit-transform 0.3s 0s;
-webkit-transition-timing-function: ease;
        transition-timing-function: ease;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.icon-pull-down-rotate {
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}



