.c-checkboard{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 100%;
}
.c-checkboard label{
    padding: 0.16rem 0.12rem;
}
.c-checkboard label + label{
    border-top: 1px solid #ccc;
}
.c-checkboard .c-checkboard-active{
    position: relative;
}
.c-checkboard .c-checkboard-active .c-checkboard-icon{
    display: block;
    color: #00a678;
    font-weight: bolder;
    width: 16px;
    height: 16px;
    position: absolute;
    right: 0.12rem;
    top: 50%;
    -webkit-transform: translate(0, calc(-16px / 2));
            transform: translate(0, calc(-16px / 2));
}
.c-checkboard .c-checkboard-icon{
    display: none;
}





