.c-dayspicker .c-btn{
  line-height:0.22rem;
  font-size: 12px;
  text-align:center;
  border-width:1px;
  border-style:solid;
  border-color: #999;
  color: #999;
  box-sizing: border-box;
  margin-right:0.15rem;
  float:left
}
.c-dayspicker .c-btn.c-btn-base{
  width:0.5rem;
  height:0.22rem;
  border-radius:0.04rem
}
.c-dayspicker .c-btn.c-btn-on{
  color:#59B8FC;
  border-color:#59B8FC;
  background:none
}
.c-dayspicker-quick{
  padding:0.12rem 0;
  border-bottom:#e5e5e5 solid 1px;
  overflow:hidden;
}
.c-dayspicker-body{
  width:100%;
  padding:0 0 0.12rem 0;
  overflow:hidden;
}
.c-dayspicker-body .c-btn{
  margin-top:0.12rem;
}



