.c-range{
  display: inline-block;
  width:100%;
  padding:0 calc(0.24rem/2);
  box-sizing: border-box;
}
.c-range-slider{
  width:100%;
  padding: 0.16rem 0;
  box-sizing: border-box;
}
.c-range-slider-line{
  position: relative;
  width: 100%;
  height: 0.02rem;
  background: #ccc;
  border-radius: calc(0.02rem / 2);
}
.c-range-slider-process{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  height: 0.02rem;
  background-color: #59B8FC;
  border-radius: calc(0.02rem / 2);
}
.c-range-slider-button{
  display: inline-block;
  position:absolute;
  top:50%;
  right:0;
  margin-right: calc(-0.24rem / 2);
  margin-top: calc(-0.24rem / 2);
  width:0.24rem;
  height:0.24rem;
  border:#59B8FC solid 0.02rem;
  border-radius: 100%;
  background-image: -webkit-linear-gradient(bottom, #f9f9f9 0%, #fff 80%);
  background-image: linear-gradient(0deg, #f9f9f9 0%, #fff 80%);
  box-shadow:0 1px 2px rgba(0,0,0,0.1);
  box-sizing:border-box;
  z-index:5;
}
.c-range-slider-button .text{
  display:inline-block;
  position: absolute;
  top: calc(-0.24rem * 0.75);
  left:50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: calc(-0.24rem * 0.75);
  font-size: 14px;
  font-style: normal;
  white-space: nowrap;
}
.c-range-slider-button .fadetip-enter-active, .c-range-slider-button .fadetip-leave-active{
  -webkit-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease;
}
.c-range-slider-button .fadetip-enter, .c-range-slider-button .fadetip-leave-active{
  opacity: 0;
}
.c-range-slider-step{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width:100%;
  height: 0.02rem;
  background-color: #59B8FC;
}
.c-range-slider-step .step-dot{
  display: inline-block;
  position:absolute;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
  width: 0.04rem;
  height: 0.04rem;
  background: #59B8FC;
  border-radius:100%;
}
.c-range-dots{
  position: relative;
  width:100%;
  height: 0.24rem;
  box-sizing: border-box;
}
.c-range-dots .dot-icon, .c-range-dots .dot-item{
  display:inline-block;
  position: absolute;
  top:0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 0.24rem;
  line-height: 0.24rem;
  font-size: 14px;
  white-space: nowrap
}
.c-range-dots .dot-icon:first-child, .c-range-dots .dot-item:first-child{
  -webkit-transform: translateX(-calc(0.24rem/2));
          transform: translateX(-calc(0.24rem/2));
}
.c-range-dots .dot-icon:last-child, .c-range-dots .dot-item:last-child{
  -webkit-transform: translateX(-calc(0.24rem/2));
          transform: translateX(-calc(0.24rem/2));
}
.c-range-dots .dot-icon{
  font-size:0.22rem;
}



