    position: relative;
    width: 100%;

    background-color: #fff;
    overflow: hidden;
}
.c-picker-slideIn{
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
}
.c-picker-body{
    -webkit-box-flex: 1;
        -ms-flex: auto;
            flex: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-mask-box-image: -webkit-linear-gradient(bottom, transparent, transparent 5%, white 20%, white 80%, transparent 98%, transparent);
    -webkit-mask-box-image: linear-gradient(to top, transparent, transparent 5%, white 20%, white 80%, transparent 98%, transparent);

    height: calc((16px + 14px) * 7);
    padding: 0 .36rem;

    position: relative;
    left: 0;
    top: 0;
}
.c-picker-col{
   -webkit-box-flex: 1;
       -ms-flex: auto;
           flex: auto;

    overflow: hidden;
    /*width: 50%;*/


    position:relative;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-perspective:700px;
    perspective:700px;
}
.c-picker-col-wrapper{
    -webkit-transition: -webkit-transform 300ms ease-out;
    transition: -webkit-transform 300ms ease-out;
    transition: transform 300ms ease-out;
    transition: transform 300ms ease-out, -webkit-transform 300ms ease-out;
}
.c-picker-col-wrapper-3d{
    -webkit-transition: -webkit-transform 300ms ease-out;
    transition: -webkit-transform 300ms ease-out;
    transition: transform 300ms ease-out;
    transition: transform 300ms ease-out, -webkit-transform 300ms ease-out;
    /*padding: 0 .5rem;*/
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
}
.c-picker-item{
    height: calc(16px + 14px);
    line-height: calc(16px + 14px);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #333;
    font-size: 16px;
    width: 100%;
    text-align: center;
    background-color:#fff;
}
.c-picker-col-wrapper-3d > .c-picker-item{
    height: calc(16px + 14px);
    line-height: calc(16px + 14px);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #333;
    font-size: 16px;
    width: 100%;
    text-align: center;
	  -webkit-transform-style: preserve-3d;
	  transform-style: preserve-3d;
    white-space: nowrap;
    position:absolute;
    overflow: hidden;
    text-overflow: ellipsis;
    left: 0;
    top: 0;
    box-sizing: border-box;
    -webkit-transition: .3s;
    transition: .3s;
    -webkit-transform-origin: center center -90px;
    transform-origin: center center -90px;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}
.c-picker-mask-top{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(calc(16px + 14px) * 3 - 1px);
    background-color: rgba(255,255,255,0.6);
    border-bottom: 1px solid #ccc;
    pointer-events: none;
}
.c-picker-mask-bottom{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: calc(calc(16px + 14px) * 3 - 1px);
    background-color: rgba(255,255,255,0.6);
    border-top: 1px solid #ccc;
    pointer-events: none;
}

