.c-switch {

    position: relative;
		display: inline-block;
		width: 0.53rem;
		height: 0.26rem;
    /*box-sizing: border-box;*/
border-width: 1px;
border-style: solid;
    border-color: #ccc;
		border-radius: 50px;
    -webkit-box-flex:0;
        -ms-flex:none;
            flex:none;
}
.c-switch .c-switch-button {

    top: calc(0.26rem / 2);

    left: 0;

    width: 0.1rem;

    height: 0.1rem;

    -webkit-transform: translate(0.08rem, calc(-0.1rem / 2));

            transform: translate(0.08rem, calc(-0.1rem / 2));

    position: absolute;

    border-radius: 100%;

    -webkit-transition: -webkit-transform 0.3s 0s;

    transition: -webkit-transform 0.3s 0s;

    transition: transform 0.3s 0s;

    transition: transform 0.3s 0s, -webkit-transform 0.3s 0s;

    -webkit-transition-timing-function: ease;

            transition-timing-function: ease;

    z-index: 2;

    background-color: #ccc;
}
.c-switch.is-disable {
    /* TODO: border-color, background-clor */
}
.c-switch.is-on {

    border-color: #59B8FC;
}
.c-switch.is-on .c-switch-button {

    background-color: #59B8FC;

    -webkit-transform: translate(calc(0.53rem - 0.08rem - 0.1rem),
          calc(-0.1rem / 2));

            transform: translate(calc(0.53rem - 0.08rem - 0.1rem),
          calc(-0.1rem / 2));
}
.c-switch.is-off {}



