/*modal**/
.c-input {
    background: #fff none repeat scroll 0 0;
    position: relative;
    padding-right: 0.2rem;
}
.c-input .c-input-delquery {
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 0;
    font-size: 18px;
    color: #999;
}
.c-input .c-input-key {
    /*input框*/
    background: none;
    border: 0;
    font: 16px/22px arial;
    height: 0.16rem;
    outline:none;
    padding: 1px 0;
    padding-right: 0.2rem;
    width: 100%;
}



