.c-panel {
    width: 3.12rem;
    margin: auto;
    box-sizing: border-box;

    font-size: 16px;

    background: #fff;
border-width: 1px;
border-style: solid;
box-shadow: 0 1px 1px rgba(0,0,0,.01);
    border-color: #eee;
    border-radius: 3px
}
.c-panel:not(:first-child) {
    margin-top: 10px
}
.c-panel-header {
    padding: 0.12rem;

    min-height: calc(2 * 0.26rem);
    border-bottom: 1px solid #eee;

    font-size: 16px;
    color: #333;
    box-sizing: border-box;
}
.c-panel-extra {
    width: calc(2 * 0.26rem);
    text-align: right;
}
.c-panel-extra .icon {
    font-size: 18px;
    display: inline-block;
}
.c-panel-body {
    padding: 0.12rem;

/*    padding-left: 0.12rem;
    padding-right: 0.12rem;
*/
}
.c-panel-introduce{
      font-size: 12px;
      padding-top: 0.08rem;
}
