.c-counter {

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;

  width: 1.03rem;
  height: 0.35rem;
  overflow: hidden;
border-width: 1px;
border-style: solid;

  border-color: #ccc;
  border-radius: calc(0.35rem / 2);
}
.c-counter > .c-btn{

  width: 50%;

  height: 100%;

  border: none;

  border-radius: 0;

  color: #666;
}
.c-counter .c-counter-inc {

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;

  -webkit-box-pack: center;

      -ms-flex-pack: center;

          justify-content: center;

  -webkit-box-align: center;

      -ms-flex-align: center;

          align-items: center;

  position: relative;
}
.c-counter .c-counter-inc i {

  font-style: normal;
}
.c-counter .c-counter-inc:after {

  content: '';

  position: absolute;

  right: 0;

  width: 1px;

  height: 100%;

  background-color: #ccc;
}
.c-counter .c-counter-dec {

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;

  -webkit-box-pack: center;

      -ms-flex-pack: center;

          justify-content: center;

  -webkit-box-align: center;

      -ms-flex-align: center;

          align-items: center;
}
.c-counter .c-counter-dec i {

  font-style: normal;
}
.c-counter .c-counter-disabled{

  color: #efefef;
}




